.sub-menu {
    padding-left: 0;
    margin-bottom: 0;
    list-style-type: none;
    line-height: 2.2;
}

.sub-menu:last-child {
	padding-bottom: 50px;
}

.sub-menu li {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    cursor: pointer;
    font-weight: 500;
}

.sub-menu li:hover,
.sub-menu li.active {
    color: orange;
}

.sub-menu li:hover .badge,
.sub-menu li.active .badge {
    background: orange;
}

.sub-menu li.active { font-weight: 600 }

h3 {
    margin-bottom: 30px;
    font-weight: 700;
}

h4 {
    font-weight: 700;
}