.navv {
	padding-top: 30px;
}

.nav .navbar-header {
	width: 78px;
}
.nav .navbar-brand {
	padding: 0;
}

.nav .navbar-nav {
	text-transform: uppercase;
	font-weight: bold;
}

.nav .navbar-nav li a {
	color: black;
}

.flex {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.icons-menu .fas {
	font-size: 20px;
	cursor: pointer;
} 
.icons-menu .fas:hover,
.nav .navbar-nav li a:hover{
	color: orange;
}
.icons-menu .fas:first-child{
	padding-right: 10px;
}

.title {
	margin: 0;
	font-size: 50px;
}
