.inner {
	margin-bottom: 30px;
	cursor: pointer;
	border: 2px solid #e7e7e7;
	transition: border .2s ease;
	border-radius: 4px;
	display: block;
}

.card-img {
	width: 100%;
	transition: transform .2s ease;
}
.content-cont {
	background-color: orange;
	padding: 15px;
	color:black;
	text-align: left;
}

.content-cont h2 {
	margin-top: 0;
	font-size: 16px;
	font-weight: bold;
}

.img-cont {
	padding: 20px;
	width: 250px;
	height: 180px;
}

.inner:hover {
	border-color: orange;
	text-decoration: none;
}

.inner:hover .card-img {
	transform: scale(1.06);
}